<template>
  <div id="app">
    <v-app class="app-font">
      <AppNavBar />
      <router-view />
      <AppFooter />
    </v-app>
  </div>
</template>

<script>
// @ is an alias to /src
import AppNavBar from "@/components/common/AppNavBar.vue";
import AppFooter from "@/components/common/AppFooter.vue";

export default {
  name: "app",
  head: {
    title: "Travelfreak Vacations",
    titleTemplate: "%s - Travelfreak Vacations",
    link: [{ rel: "canonical", href: "https://www.travelfreakvacations.com" }],
    meta: [
      {
        name: "description",
        content:
          "Find great places to stay, eat, shop, or visit from local experts."
      },
      // OpenGraph data (Most widely used)
      { property: "og:title", content: "Travelfreak Vacations" },
      { property: "og:site_name", content: "Travelfreak Vacations" },
      // The list of types is available here: http://ogp.me/#types
      { property: "og:type", content: "website" },
      // Should be the same as your canonical link, see below.
      { property: "og:url", content: "https://www.travelfreakvacations.com" },
      {
        property: "og:image",
        content: "https://www.travelfreakvacations.com/images/tfv-logo.png"
      },
      // Often the same as your meta description, but not always.
      {
        property: "og:description",
        content:
          "Find great places to stay, eat, shop, or visit from local experts."
      }
    ]
  },
  components: {
    AppNavBar,
    AppFooter
  }
};
</script>

<style>
body .app-font {
  font-family: "Poppins", Arial, sans-serif;
  background: #fff;
  font-size: 14px;
  line-height: 1.8;
  font-weight: 300;
  color: #4d4d4d;
}

h1 {
  /*  font-family: "Work Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";*/
  font-size: 52px;
  color: #fff;
  line-height: 1.2;
}
h2 {
  font-size: 25px;
}
h3 {
  font-size: 20px;
  font-weight: normal;
}
strong {
  font-weight: bold;
}

.v-btn {
  text-transform: none;
}
.v-expansion-panels {
  z-index: 0;
}

.slide-fade-enter-active {
  transition: all 1s ease;
}
.slide-fade-leave-active {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to
  /* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateY(30px);
  opacity: 0;
}
</style>


